  padding: 0;
  background-color: #0b2f68;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

button {
  border: none;
  padding: 0 1rem;
  font-size: 14px;
  color: #fff;
  background-color: transparent;
  cursor: pointer;
}

h2 {
  padding: 0 1rem;
  font-size: 20px;
  color: #fff
}


